:root{
  --setDisplayPermissionGranted: none;
  --setDisplayStart: flex;
  --hideUI: none;
}

.startElement{
  dispay: var(--setDisplayStart);
}

.hiddenUI{
  display: var(--hideUI)
}

#startDiv{
  width: 100%;
  height: 100%;
  margin: 0%;
  background-color: #181a1d;
  padding-top: 0%;
  padding-bottom: 100%;
}

.logosDiv{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15%;
}
.cupoLogo{
  width: 50%;
  
}
.planetariumLogo{
  width: 15%;
  padding-top: 5%;
}

#playButton{
  display: flex;
  margin: auto;
  background-color: #202221a3;
  /*font-size: calc(20px + 3vmin);*/
  margin-top: 30%;
}

.PulsatingText {
  /* Chrome, Safari, Opera */
 -webkit-animation: PulsatingText 5s ease-in-out; 
 -webkit-animation-iteration-count: infinite; 
 
 /* Internet Explorer */
 -ms-animation: PulsatingText 5s ease-in;
 
 /* Standard Syntax */
 animation: PulsatingText 5s ease-in-out;
 animation-iteration-count: infinite;
}
/* Chrome, Safari, Opera */
@-webkit-keyframes PulsatingText{
  50%{color:rgb(107, 243, 234);}	
 110%{color: rgb(255, 255, 255);}
}

@keyframes PulsatingText{
  50%{color:rgb(107, 243, 234);}	
	110%{color: rgb(255, 255, 255);}
}
@-ms-keyframes PulsatingText{
  50%{color:rgb(107, 243, 234);}	
	110%{color: rgb(255, 255, 255);}
}

#titleGame{
  color: rgba(99, 99, 99, 0.915);
  font-size: 18px;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  padding-top: 10%;
  padding-bottom:10%;
  margin-top: 0%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: var(--setDisplayPermissionGranted);
  background-color: #282c34;
  min-height: 100vh;
  /*display: flex;*/
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.colDiv{
  flex-grow: 1;
  text-align: center;
  margin-left: 40px;
  margin-right: 40px;
}
.rowDiv{
  display: flex;
}
